.new-method {
  padding-bottom: 80px;
  font-family: 'Roboto';

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  &__bg {
    // sass-lint:disable-block indentation
    background:
      linear-gradient( 105deg, #F3F4F5 55%, transparent 55.1%),
      linear-gradient( 0deg, #222B34 100%, transparent 55.1%);
    height: 370px;
  }

  &__item {
    padding: 0 50px;
    height: 100%;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
  }

  &__info {

    .intro-box {

      .h2 {
        font-size: 26px;
        line-height: 34px;
        color: #222B34;
      }
    }
  }

  &__list {
    width: 45%;
    flex-basis: 45%;
    min-width: 45%;
  }
}


@media (max-width: 768px) {
  .new-method {
    &__wrap {
      max-width: 90%;
      margin: 0 auto;
    }

    &__bg {
      // sass-lint:disable-block indentation
      background:
        linear-gradient( 175deg, #F3F4F5 52%, transparent 52.1%),
        linear-gradient( 0deg, #222B34 100%, transparent 52.1%);
      height: auto;
      flex-wrap: wrap;
    }

    &__item {
      padding: 50px 50px;
      height: auto;
      display: flex;
      align-items: center;
      align-content: center;
      justify-content: center;
    }

    &__info {
      .intro-box {
        .h2 {
          font-size: 20px;
          margin-bottom: 25px;
        }
      }
    }

    &__list {
      width: 100%;
      flex-basis: 100%;
      min-width: 100%;
    }
    .intro-box.p-decor p {
      padding-left: 30px;
      &::before {
        width: 30px;
        left: -5px;
      }
    }
    .new-method__actions {
      padding-left: 30px;
    }
  }
}

@media (max-width: 679px) {
  .new-method__wrap {
      max-width: 100%;
      margin: 0 auto;
  }
}
