.copy-transaction {

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  &__cards {
    padding-top: 50px;
  }
}

@media (max-width: 768px) {
  .cut-bubble-message__content {
    .h2 {
      font-size: 20px;
      line-height: 1.4;
    }
    p {
      font-size: 14px;
      line-height: 1.8;
    }
  }
  .cut-bubble-picture {
    position: relative;
    text-align: left;
    &__box {
      width: 100%;
      flex-basis: 400px;
      min-width: 400px;
      img {
        padding-right: 0;
      }
    }
  }
}



@media #{$tablet_gt} {
}

@media (max-width: 678px) {

  .copy-transaction {
    overflow: hidden;
    &__wrap {
      display: block;
      max-width: 100%;
    }
    .owl-stage-outer {
      padding-top: 10px;
      margin-top: -10px;
      padding-bottom: 30px;
      .owl-stage {
        display: flex;
      }
    }
    .owl-nav.disabled {
      display: none;
    }
    .icon-cards {
      padding-bottom: 0;
    }
    .icon-card__box {
      flex-wrap: wrap;
      min-width: 200px;
      .icon-card__icon {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 30px;
        &:before {
          top: 20px;
        }
      }
    }
    .icon-card__content {
      padding-right: 0;
      text-align: center;
      padding-left: 25px;
      padding-right: 25px;
    }
  }
  .copy-transaction__wrap {
    padding-top: 0;
    .owl-nav.disabled {
      display: none;
    }
  }
  .copy-transaction__bg {
    -webkit-clip-path: polygon(0 calc(0% + 10px), 50% calc(0% + 10px), 100% 0, 100% calc(100% - 10px), 50% calc(100% - 10px), 0 100%);
    clip-path: polygon(0 calc(0% + 10px), 50% calc(0% + 10px), 100% 0, 100% calc(100% - 10px), 50% calc(100% - 10px), 0 100%);
  }
  .copy-transaction .icon-cards {
    margin: 0;
  }
  .copy-transaction .icon-card {
    flex: 1 1 100%;
    flex-basis: 100%;
    max-width: 100%;
    min-width: 100%;
    margin-bottom: 60px;
    .icon-card__content p {
      line-height: 1.4;
    }

  }
}
