.security-of-means {
  padding-top: 90px;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;
    justify-content: space-between;
    @media #{$tablet} {
      flex-wrap: wrap;
      .icon-card:nth-child(even) {
        .icon-card__content {
          padding-right: 0;
        }
      }
    }
    @media #{$tablet_lt} {
      flex-wrap: wrap;
      padding-left: 20px;
      padding-right: 0;
      .owl-stage-outer {
        padding-top: 10px;
        margin-top: -10px;
      }
    }
  }

  &__content {
    position: relative;
    flex-basis: 450px;
    min-width: 450px;
    @media #{$tablet} {
      flex-basis: 100%;
      min-width: 100%;
      margin-bottom: 40px;
      padding-right: 20px;
      .intro-box.text-left h2 {
        text-align: center;
        span {
          display: inline;
        }
      }
      .intro-box.p-decor p {
        padding-left: 0;
        margin-top: 60px;
        text-align: center;
        &:before {
          top: -35px;
          left: 50%;
          transform: translateX(-50%) rotate(120deg);
        }
      }
    }
    @media #{$tablet_lt} {
      flex-basis: 100%;
      min-width: 100%;
      margin-bottom: 40px;
      .intro-box.text-left h2 {
        // text-align: center;
        span {
          // display: inline;
        }
      }
    }
    &:before {
      position: absolute;
      content: '';
      background-repeat: no-repeat;
      background-size: auto;
      background-position: center;
      background-image: url('../images/fingerprint.png');
      width: 365px;
      height: 435px;
      z-index: 0;
      left: -160px;
      top: -100px;
      @media #{$tablet} {
        top: -90px;
      }
      @media #{$tablet_lt} {
        top: -90px;
      }
    }

    .intro-box {
      position: relative;
      z-index: 1;
    }
  }

  &__cards {
    padding-left: 0;
    max-width: 100%;
    overflow: hidden;
    padding-top: 20px;

    @media #{$tablet_lt} {
      .icon-card {
        width: 100%;
        max-width: 170px;
        min-width: 170px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 60px;
        &__box {
          flex-wrap: wrap;
        }
        &__icon {
          margin: 0 auto 30px;
          &:before {
            top: 25px;
          }
        }
        &__content {
          padding-right: 0;
        }
      }
    }

    .icon-cards--black {
      padding-left: 40px;
      @media #{$tablet} {
        padding-left: 0;
      }
      @media #{$tablet_lt} {
        padding-left: 10px;
        padding-right: 0;
        text-align: center;
      }
    }
  }
}
