.terminal-apps {
  background-color: #fff;
  padding-top: 60px;
  padding-bottom: 90px;
  padding-bottom: 90px;
  font-family: 'Roboto';
  overflow: hidden;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }

    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }

    @media #{$tablet} {}
  }

  &__headline {
    max-width: 560px;

    .h2 {
      font-size: 26px;
      line-height: 34px;
      color: #6E7A86;
      margin: 0;
    }
  }

  &__actions {
    margin-top: 20px;
    display: flex;
    align-items: flex-start;
    align-content: flex-start;

    .button {
      box-sizing: border-box;
      margin-bottom: 20px;
      flex-basis: 40px;
      min-height: 40px;
      min-width: 130px;
      margin-right: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__mobile,
  &__desktop {
    position: relative;

    &--picture {
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      position: relative;
    }
  }

  &__desktop {
    position: relative;
    .terminal-apps__headline {
      max-width: 375px;
    }
  }
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: .5fr .5fr;
  grid-template-areas: "a b" "a c";
}

.grid-container--a {
  grid-area: a;
  padding-right: 30px;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.grid-container--b {
  grid-area: b;
}

.grid-container--c {
  grid-area: c;
  margin-top: 55px;
}


@media all and (-ms-high-contrast:none) {
  .grid-container {
    display: -ms-grid;
    -ms-grid-columns: 1fr 1fr;
    -ms-grid-rows: 1fr 1fr;
  }

  .grid-container--a {
    -ms-grid-row: 1;
    -ms-grid-row-span: 2;
    -ms-grid-column: 1;
    -ms-grid-column-span: 1;
  }

  .grid-container--b {
    -ms-grid-row: 1;
    -ms-grid-row-span: 1;
    -ms-grid-column: 2;
    -ms-grid-column-span: 1;
  }

  .grid-container--c {
    -ms-grid-row: 2;
    -ms-grid-row-span: 1;
    -ms-grid-column: 2;
    -ms-grid-column-span: 1;
  }

}

@media (max-width: 900px) {
  .grid-container {
    display: block;
    max-width: 635px;
    margin-left: auto;
    margin-right: auto;
  }

  .terminal-apps__desktop {
    display: flex;
    align-items: flex-start;
    margin-bottom: 70px;
    .terminal-apps__actions {
      margin-top: 0;
      width: 50%;
      flex-direction: initial;
      justify-content: center;
    }
  }
  .terminal-apps__mobile {
    margin-top: 70px;
    .terminal-apps__actions {
      flex-direction: initial;
      .button {
        margin-right: 25px;
      }
    }
  }

  @media all and (-ms-high-contrast:none) {
    .grid-container {
      display: block;
    }
  }
}



@media (max-width: 636px) {
  .grid-container {
    display: block;
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
  }

  .terminal-apps__desktop {
    display: flex;
    align-items: flex-start;
    margin-bottom: 70px;
    flex-wrap: wrap;
    .terminal-apps__headline {
      width: 100%;
    }
    .terminal-apps__actions {
      margin-top: 30px;
      width: 100%;
      flex-direction: initial;
      justify-content: start;
    }
  }
  .terminal-apps__mobile {
    margin-top: 70px;
    .terminal-apps__actions {
      flex-direction: initial;
      .button:not(:last-child) {
        margin-right: 25px;
      }
    }
  }

  .h2 > span {
    display: inline;
  }

  .grid-container--a {
    padding-right: 0;
  }

  .grid-container {
    .h2 {
      font-size: 20px;
      line-height: 1.4;
    }
  }

  @media all and (-ms-high-contrast:none) {
    .grid-container {
      display: block;
    }
  }
}


@media (max-width: 767px) {
  .terminal-apps {
    &__desktop {
      position: relative;
      .terminal-apps__headline {
        max-width: 85%;
        min-width: 300px;
      }
    }
  }
}
