.our-value {
  padding-bottom: 60px;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  &__cards {
    margin-top: 30px;
    margin-bottom: 30px;

    @media #{$mobile} {
      overflow: scroll;
    }
    @media #{$mobile-big} {
    }
    @media #{$tablet} {
    }
  }
}

@media (max-width: 992px) {
  .our-value {
    &__wrap {
      max-width: 100%;
      overflow: hidden;
    }

    .intro-box {
      max-width: 90%;
      margin-left: auto;
      margin-right: auto;
    }

    .number-card {
      min-width: 407px;
      max-width: 407px;
    }
    .owl-item:nth-child(1) .number-card__box {
      -webkit-clip-path: polygon(0 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
      clip-path: polygon(0 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    }
    .owl-item:nth-child(2) .number-card__box {
      -webkit-clip-path: polygon(calc(0% + 25px) 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
      clip-path: polygon(calc(0% + 25px) 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
      .number-card__header-icon {
        margin-left: 16px;
      }
    }
    .owl-item:nth-child(3) .number-card__box {
      -webkit-clip-path: polygon(calc(0% + 25px) 0, 100% 0%, 100% 100%, 0% 100%);
      clip-path: polygon(calc(0% + 25px) 0, 100% 0%, 100% 100%, 0% 100%);
      .number-card__header-icon {
        margin-left: 16px;
      }
    }

    .number-card__header .number-card__header-title {
      padding-right: 20px;
    }
    .number-card__content {
      margin-top: auto;
      margin-bottom: 24px;
    }
  }
  .our-value__cards {
    .owl-dots.disabled,
    .owl-nav.disabled {
      display: none;
    }
    .owl-stage {
      padding-left: 30px;
    }
    .owl-stage-outer {
      max-width: 100%;
      padding-top: 30px;
      padding-bottom: 30px;

      overflow: hidden;
    }
  }
}
@media (max-width: 420px) {
  .our-value .our-value__cards .number-card {
      min-width: 300px;
      max-width: 300px;
  }
}
