$partners-good-deg:  30px;

.partners-good {
  padding-top: 50px;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    justify-content: space-between;
    padding-bottom: 100px;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  &__description {
    flex-basis: 680px;
    min-width: 680px;
    margin-right: 50px;

    p {
      padding-right: 50px;
    }
  }

  &__pic {
    position: relative;

    &:after {
      position: absolute;
      content: '';
      background-color: #EA252C;
      -webkit-clip-path: polygon(
        0 calc(0% + #{$partners-good-deg}),
        100% 0,
        90% 88%,
        0% 100%
      );
      clip-path: polygon(
        0 calc(0% + #{$partners-good-deg}),
        100% 0,
        90% 88%,
        0% 100%
      );
      width: 450px;
      height: 310px;
      right: -50px;
      bottom: 20px;
      z-index: -1;
    }

    &-img {
      background-color: #232D3A;
      -webkit-clip-path: polygon(
        0 0,
        100% 0,
        calc(100% - #{$partners-good-deg}) 100%,
        0% 100%
      );
      clip-path: polygon(
        0 0,
        100% 0,
        calc(100% - #{$partners-good-deg}) 100%,
        0% 100%
      );
      width: 450px;
      height: 280px;
      position: relative;
      z-index: 0;

      img {
        width: 100%;
        vertical-align: middle;
        object-fit: cover;
        position: relative;
      }
    }
  }
}

@media #{$tablet_part} {
  .partners-good {
    overflow: hidden;
    &__wrap {
      max-width: 90%;
      min-width: 320px;
    }
  }
  .partners-good__description {
    max-width: 80%;
    min-width: 330px;
  }
}

@media (max-width: 678px) {
  .partners-good {
    .intro-box.p-decor p:before {
      width: 30px;
    }
    padding-top: 0;
    &__wrap {
      padding-bottom: 40px;
      max-width: 90%;
      min-width: 320px;
      flex-wrap: wrap;
    }
  }
  .partners-good__pic {
    margin-top: 60px;
    max-width: 100%;
    &:after {
      bottom: 5%;
      height: 110%;
      width: 100%;
      left: 10%;
    }
  }
  .partners-good__pic-img {
    max-width: 100%;
    height: auto;
  }
  .partners-good__description {
    max-width: 80%;
    min-width: 330px;
  }
}
