.advantage-terminal {
  background-color: #F3F4F5;
  padding-top: 60px;
  padding-bottom: 60px;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  .intro-box {
    padding-left: 100px;
    padding-right: 100px;

    p span {
      display: block;
    }
  }

  &__cards {
    padding-top: 70px;
  }

}
@media (max-width: 690px) {
  .advantage-terminal__wrap {
    max-width: 100%;
  }
  .advantage-terminal .intro-box {
    padding-left: 0;
    padding-right: 20px;
  }
}
