.headline-slider {
  width: 100%;
  background: linear-gradient(
    108.2deg,
    #232D3A 0%,
    #232D3A 80%,
    #4A5261 80.1%,
    #4A5261 95%,
    #232D3A 95.1%,
    #232D3A 95%,
  );
  height: 600px;
  position: relative;
  @media #{$tablet} {
    height: 730px;
  }

  &__item {
    width: 100%;
    height: 600px;
    @media #{$tablet} {
      height: 730px;
    }
  }

  .headline-slider__list {
    overflow: visible !important;
  }

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;
    position: relative;
    z-index: 1;
    .headline-features {
      position: absolute;
      bottom: 80px;
      @media #{$tablet} {
        bottom: 70px;
      }
    }
  }

  &__list {
    position: relative;
  }

  .owl-dots {
    text-align: center;
    -webkit-tap-highlight-color: transparent;

    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    bottom: 20px;
    z-index: 1;

    .owl-dot {
      display: inline-block;
      zoom: 1;
      /* *display: inline; */

      span {
        width: 8px;
        height: 8px;
        margin: 4px 4px;
        background: #ffffff;
        display: block;
        -webkit-backface-visibility: visible;
        transition: opacity 200ms ease;
        border-radius: 30px;
      }

      &.active span,
      &:hover span {
        background: #EA252C;
      }
    }
  }
}

.headline--main {
  position: relative;
  background: linear-gradient(
    108.2deg,
    #232D3A 0%,
    #232D3A 80%,
    #4A5261 80.1%,
    #4A5261 95%,
    #232D3A 95.1%,
    #232D3A 95%,
  );
  height: 600px;
  @media #{$tablet} {
    height: 730px;
  }

  &:before {
    position: absolute;
    z-index: 0;
    content: '';
    top: -10px;
    left: 0;
    width: 100%;
    height: calc(100% + 20px);
    background-repeat: no-repeat;
    background-position: 76% 100%;
    background-size: cover;
    mix-blend-mode: luminosity;
  }

  .headline__wrap {
    padding-top: 80px;
    padding-bottom: 200px;
    @media #{$tablet} {
      padding-bottom: 240px;
    }
    z-index: 2;
  }

  .headline__actions {
    margin-top: auto;
  }

  .headline__content {
    max-width: 820px;

    .h2,
    .h2 span,
    p {
      color: #ffffff;
    }

    .h2 {
      font-size: 46px;
      line-height: 60px;
      margin-bottom: 40px;

      span {
        display: block;
      }
    }

    p {
      max-width: 490px;
    }
  }
}

/* Битконин */
.headline--bitcoin {

  &:before {
    background-image: url('../images/main/headline/headline-main-page-bitcoin.png');
    mix-blend-mode: normal;
    @media #{$tablet} {
      background-image: url('../images/main/headline/headline-main-page-bitcoin_tablet.png');
      // background-position: 70% 100%;
    }
  }

  @media #{$tablet} {
  .headline__content {
    .h2 {
        max-width: 450px;
        font-size: 36px;
        line-height: 1.3;
      }
      p {
        font-size: 16px;
        line-height: 1.6;
        max-width: 355px;
        box-sizing: border-box;
      }
    }
  }
  .headline__picture {
    position: absolute;
    right: -50px;
    top: 20px;

    img {
      vertical-align: middle;
    }
  }
  @media (max-width: 679px) {
      .headline-slider .headline-slider__item &.headline--main {
        height: 615px;
        // height: auto;
        background-color: #222B34;
        background-image: linear-gradient(to bottom, transparent 0, transparent 150px, #232D3A 340px, #222B34 100%), url(../images/main/headline/headline-main-page-bitcoin_mobile.png);
        background-position: 0 0, 41% -31px;
        background-size: auto, 651px;
        background-repeat: no-repeat;
        &::before {
          display: block;
          content: '';
          width: 100%;
          position: absolute;
          bottom: 100px;
          height: auto;
          top: 370px;
          left: 0;
          background: #EA252C;
          transform: skewY(-12.4deg);
          mix-blend-mode: unset !important;
        }
        &:after {
          display: block;
          content: '';

          height: 60px;
          width: 100%;
          position: absolute;
          bottom: 40px;
          left: 0;
          background: #4A5261;
          transform: skewY(-12.4deg);
        }
        .headline__actions {
          margin-top: 0;
        }
        .headline__wrap {
          justify-content: start;
          padding-top: 40px;
          padding-bottom: 0;
        }
        .headline__content {
          .h2 {
            margin-bottom: 50px;
            line-height: 1.48;
          }
          p {
            margin-bottom: 24px;
            max-width: 190px;
            font-weight: 300;
            font-size: 14px;
            line-height: 1.48;
            &:before {
              width: 20px;
              left: 0;
              top: -27px;
            }
          }
        }
        .db {
          display: block !important;
        }
        .headline-actions {
          .button {
            min-width: 185px;
            padding: 19px 25px 20px 25px;
          }
        }
    }
  }
}

