.growth-plan {
  padding-top: 100px;
  padding-bottom: 100px;
  height: auto;
  clear: both;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;
    position: relative;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  .intro-box {
    margin-bottom: 30px;
  }
}
@media (max-width: 768px) {
  .growth-plan {
    padding-top: 70px;
    .plan-list__description {
      font-size: 16px;
      line-height: 1.6;
      max-width: 300px;
    }
    .plan-list__year {
      font-size: 20px;
    }
  }
}
@media (max-width: 534px) {
  .growth-plan {
    padding-top: 50px;
    .plan-list__content {
      flex-wrap: wrap;
    }
    .plan-list__description {
      font-size: 14px;
      line-height: 1.6;
      max-width: 300px;
    }
    .plan-list__year {
      font-size: 20px;
    }
  }
}

