$partners-steps-height:      800px;   // высота блока
$partners-steps-deg:        -5deg;    // угол поворота фона
$partners-steps-bg-height:   63px;    // компенсация поворта
$partners-steps-pt:          52px;    // отступ сверху
$partners-steps-pb:          114px;   // отступ снизу


.partners-steps {
  position: relative;
  min-height: $partners-steps-height;
  height: $partners-steps-height;
  padding-top: $partners-steps-pt;
  padding-bottom: $partners-steps-pb;

  &__bg {
    background-color: #F3F4F5;
    position: absolute;
    width: 100%;
    height: calc(100% - #{$partners-steps-bg-height});
    left: 0;
    top: 0;
    transform: skewY(#{$partners-steps-deg});
    z-index: 0;
  }

  &__wrap {
    position: relative;
    z-index: 1;
    max-width: 1180px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
  }

  .intro-box {

    .h2 {
      margin-bottom: 0;
    }
  }

  &__cards {
    padding-top: 50px;
  }
}
@media #{$tablet_part} {
  .partners-steps {
    height: 600px;
    min-height: 600px;
    padding-top: 82px;
  }
  .partners-steps__wrap {
    max-width: 100%;
    overflow: hidden;
    display: block;
  }
  .number-cards--width-header .number-card {
    min-width: 407px;
    max-width: 407px;
  }
  .number-cards--width-header .owl-item:nth-child(1) .number-card__box {
    -webkit-clip-path: polygon(0 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    clip-path: polygon(0 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
  }
  .number-cards--width-header .owl-item:nth-child(2) .number-card__box {
    -webkit-clip-path: polygon(calc(0% + 25px) 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    clip-path: polygon(calc(0% + 25px) 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    .number-card__header-icon {
      margin-left: 16px;
    }
  }
  .number-cards--width-header .owl-item:nth-child(3) .number-card__box {
    -webkit-clip-path: polygon(calc(0% + 25px) 0, 100% 0%, 100% 100%, 0% 100%);
    clip-path: polygon(calc(0% + 25px) 0, 100% 0%, 100% 100%, 0% 100%);
    .number-card__header-icon {
      margin-left: 16px;
    }
  }

  .number-card__header .number-card__header-title {
    padding-right: 20px;
  }

  .partners-steps__cards {
    .owl-dots.disabled,
    .owl-nav.disabled {
      display: none;
    }
    .owl-stage {
      padding-left: 30px;
    }
    .owl-stage-outer {
      max-width: 100%;
      padding-top: 30px;
      padding-bottom: 30px;
      margin-top: -30px;
      margin-bottom: -30px;

      overflow: hidden;
    }
  }
}
@media (max-width: 570px) {
  .h2 > span {
    display: block;
  }
  .number-cards--width-header .number-card:nth-child(1) .number-card__box .number-card__header-icon {
    width: 70px;
    height: 60px;
    svg use {
      width: 40px;
      height: 40px;
    }
  }
  .number-cards--width-header .number-card__header {
    height: 60px;
  }
  .number-cards--width-header .number-card__header-title {
    font-size: 16px;
  }
}
@media (max-width: 420px) {
  .number-cards--width-header .number-card {
      min-width: 300px;
      max-width: 300px;
  }
  .number-cards--width-header .number-card__content {
    padding-left: 45px;
    position: relative;
  }
  .number-cards .owl-item:nth-child(3),
  .number-cards .owl-item:nth-child(2) {
    .number-card__header-icon {
      align-self: start;
    }
    .number-card__header {
      height: auto;
    }
  }
}
