.text-investor {
  padding-top: 80px;
  padding-bottom: 60px;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }
  .intro-box {
    padding-left: 100px;
    padding-right: 100px;
    padding-bottom: 80px;

    h2 span {
      display: block;
    }
  }

  &__cards {
    position: relative;
  }

  &__left,
  &__right {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 15px;
    cursor: pointer;
    color: #97A8BC;
    transition: all .3s ease;

    &:hover {
      color: #EA252C;
    }
  }

  &__left {
    left: -65px;
  }

  &__right {
    right: -65px;
  }
}

.text-investor {
  .owl-carousel {
    display: flex;
  }
}

@media #{$tablet_gt} {
  .text-investor {
    max-width: 100%;
    overflow: hidden;
    .owl-stage-outer {
      padding-left: 40px;
    }
  }
}


@media (max-width: 754px) {
  .text-investor {
    .intro-box {
      padding-left: 0;
      padding-right: 0;
    }
  }
}
