main {
  margin-top: 60px;
}

.header {
  position: relative;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;
    height: 100%;
    width: 100%;

    @media #{$mobile} {
      max-width: 100%;
      padding: 0 0 0 15px;
    }
    @media #{$mobile-big} {
      max-width: 600px;
    }
    @media #{$tablet} {
    }
  }

  &__main {
    height: 60px;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 2px 3px rgba(0, 0, 0, .08);
    z-index: 101;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
  }

  &__logo {
    margin-right: 45px;

    @media #{$mobile} {

      svg {
        height: 30px;
        width: auto;
      }
    }
    @media #{$mobile-big} {
    }
    @media #{$tablet} {
    }
  }

  &__aside {
    margin-left: auto;
  }

  &__language {
    position: relative;

    &-list {
      position: absolute;
      top: 100%;
    }
  }

  &__cloud {
    position: fixed;
    top: 60px;
    left: 0;
    right: 0;
    width: 100%;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
  }

  &__finder {

    &-item {
      padding-left: 100px;
      padding-right: 100px;
      display: flex;
      align-items: stretch;
      align-content: stretch;

      height: 0;
      opacity: 0;
      visibility: hidden;
      position: absolute;

      .finder-menu {
        min-width: 280px;
      }

      &.is-show {
        height: auto;
        opacity: 1;
        z-index: 10;
        visibility: visible;
        position: relative;
        transition: all .35s ease;
      }
    }
  }

  /* Панель авторизации/регистрации */
  &__user {
    height: 0;
    opacity: 0;
    visibility: hidden;
    position: absolute;

    &.is-show {
      height: auto;
      opacity: 1;
      z-index: 10;
      visibility: visible;
      position: relative;
      transition: all .35s ease;
    }
  }

  /* Панель поиска */
  &__search {
    height: 0;
    opacity: 0;
    visibility: hidden;
    position: absolute;

    &.is-show {
      height: auto;
      opacity: 1;
      z-index: 10;
      visibility: visible;
      position: relative;
      transition: all .35s ease;
    }
  }
}

/* Переключатель языка */
.language-action {
  border: none;
  background-color: transparent;
  line-height: 1;
  padding: 23px 0;
  border-bottom: 3px solid transparent;
  transition: all .35s ease;

  @media #{$mobile} {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
  @media #{$mobile-big} {
    display: none;
    visibility: hidden;
    opacity: 0;

  }
  @media #{$tablet} {
  }

  &__box {
    display: flex;
    align-items: center;
    align-content: center;
  }

  &__icon {
    margin-left: 10px;
    color: #4E565D;

    svg {
      transition: all .35s ease;
    }
  }

  &:hover {

    .language-action__icon {
      color: #EA252C;
    }
  }

  &.is-open {
    border-bottom-color: #EA252C;

    .language-action__icon svg {
      transform: rotate(180deg);
    }
  }
}

.language-list {
  background-color: #fff;
  list-style-type: none;
  margin: 0;
  padding: 0;
  min-width: 140px;
  width: auto;
  left: -150%;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
  overflow: hidden;
  transition: all .35s ease;
  z-index: 0;

  visibility: hidden;
  opacity: 0;

  &__item {
    width: 100%;

    &.is-active .language-list__link {
      box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
    }
  }

  &__link {
    display: flex;
    align-items: center;
    align-content: center;
    border-bottom: 0;
    text-decoration: none;
    font-family: 'Roboto';
    font-size: 15px;
    line-height: 15px;
    color: #6E7A86;
    padding: 14px 25px;

    &:hover {
      box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);
    }
  }

  &__flag {
    margin-right: 10px;
    align-self: center;
    display: flex;

    img {
      vertical-align: bottom;
    }
  }

  &.is-show {
    visibility: visible;
    opacity: 1;
    z-index: 0;
  }
}

.search-action {
  margin-right: 45px;
  border: none;
  background-color: transparent;
  padding: 23px 0 22px 0;
  border-bottom: 3px solid transparent;
  transition: all .35s ease;
  display: flex;
  align-items: center;
  align-content: center;
  color: #A9B0B8;
  font-family: 'Roboto';
  font-size: 14px;
  line-height: 14px;

  @media #{$mobile} {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
  @media #{$mobile-big} {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
  @media #{$tablet} {
    margin-right: 40px;
  }

  &__box {
    display: flex;
    align-items: center;
    align-content: center;
  }

  &__icon {
    margin-right: 8px;
    color: #A9B0B8;
    align-self: center;
    line-height: 14px;
    display: flex;

    svg {
      transition: all .35s ease;
    }
  }

  &:hover {
    color: #6E7A86;

    .user-action__icon {
      color: #6E7A86;
    }
  }

  &.is-active {
    border-bottom-color: #EA252C;
    color: #6E7A86;
  }
}

.user-action {
  margin-left: 45px;
  border: none;
  background-color: transparent;
  padding: 23px 0 20px 0;
  border-bottom: 3px solid transparent;
  transition: all .35s ease;
  display: flex;
  align-items: center;
  align-content: center;
  color: #A9B0B8;
  font-family: 'Roboto';
  font-size: 14px;
  line-height: 14px;

  @media #{$mobile} {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
  @media #{$mobile-big} {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
  @media #{$tablet} {
    margin-right: 40px;
  }

  &__icon {
    margin-right: 8px;
    color: #A9B0B8;
    align-self: center;
    line-height: 1;

    svg {
      transition: all .35s ease;
    }
  }

  &:hover {
    color: #6E7A86;

    .user-action__icon {
      color: #6E7A86;
    }
  }

  &.is-active {
    border-bottom-color: #EA252C;
    color: #6E7A86;
  }
}

/* Логотип-ссылка в подвале */
.header-logo {
  text-decoration: none;
  border-bottom: 0;
}

.header-cloud {
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: all .35s ease;

  &__wrap {
    padding: 40px 0;
  }

  &.is-show {
    opacity: 1;
    z-index: 10;
    visibility: visible;
  }
}

.header-user {

  &__form {
    max-width: 680px;
    margin: 0 auto;

    .form-caption {
      margin-bottom: 35px;

      .h3 {
        margin-bottom: 20px;
      }
    }

    .form-more {
      margin-top: 30px;
      text-align: center;
    }
  }
}

.header-search {

  &__form {
    max-width: 580px;
    margin: 0 auto;

    .form-group {
      margin-right: 0;
    }

    .form-control {
      margin-right: 0;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      position: relative;

      &:hover {
        z-index: 3;
      }
    }

    .button {
      margin-left: -1px;
      position: relative;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }
}

.header__finder-item.has_title {
  padding-left: 0;
}

.finder-menu-icon {
  min-width: 210px;
  margin-right: 80px;
  padding-top: 15px;
  padding-bottom: 15px;
  border-right: 1px solid #EA252C;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  color: #222B34;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 500;
  &_text {
    min-width: 100%;
    text-align: center;
    margin-top: 20px;
  }
}
