$terminal-cards-height:   50px;
$terminal-cards-bottom:   15px;
$terminal-cards-deg:      20px;

.terminal-cards {
  position: relative;
  min-height: 600px;
  height: 600px;

  &:before {
    position: absolute;
    content: '';
    height: $terminal-cards-height * 2;
    width: 100%;
    background-color: #F3F4F5;
    left: 0;
    top: -1px;
    z-index: 0;
  }

  &__bg {
    background-color: #7086A2;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    -webkit-clip-path: polygon(
      0    calc(0% + #{$terminal-cards-height}),
      50%  calc(0% + #{$terminal-cards-height}),
      100% 0,
      100% calc(100% - #{$terminal-cards-height + $terminal-cards-bottom}),
      50%  calc(100% - #{$terminal-cards-height + $terminal-cards-bottom}),
      0    100%
    );
    clip-path: polygon(
      0    calc(0% + #{$terminal-cards-height}),
      50%  calc(0% + #{$terminal-cards-height}),
      100% 0,
      100% calc(100% - #{$terminal-cards-height + $terminal-cards-bottom}),
      50%  calc(100% - #{$terminal-cards-height + $terminal-cards-bottom}),
      0    100%
    );
    /*
      1---2---3
      \-------\
      6---5---4
    */
  }

  &__wrap {
    height: 100%;
    max-width: 1180px;
    margin: 0 auto;
    padding-top: $terminal-cards-height;
    padding-bottom: $terminal-cards-height + $terminal-cards-bottom;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
  }

  &__cards {
    display: flex;
    align-items: stretch;
    align-content: stretch;
  }
}

.terminal-card {
  font-family: 'Roboto';
  height: 270px;
  position: relative;
  flex: 1;

  &:nth-child(1) {
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, .1));

    .terminal-card__box {
      -webkit-clip-path: polygon(
        0 0,
        100% 0%,
        calc(100% - #{$terminal-cards-deg}) 100%,
        0% 100%
      );
      clip-path: polygon(
        0 0,
        100% 0%,
        calc(100% - #{$terminal-cards-deg}) 100%,
        0% 100%
      );

      &:before {
        width: 70px;
        left: -10px;
      }
    }
  }

  &:nth-child(2) {
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, .1));

    .terminal-card__box {
      -webkit-clip-path: polygon(
        calc(0% + #{$terminal-cards-deg}) 0,
        100% 0%,
        calc(100% - #{$terminal-cards-deg}) 100%,
        0% 100%
      );
      clip-path: polygon(
        calc(0% + #{$terminal-cards-deg}) 0,
        100% 0%,
        calc(100% - #{$terminal-cards-deg}) 100%,
        0% 100%
      );
      box-shadow: none;
    }
  }

  &:nth-child(3) {
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, .1));

    .terminal-card__box {
      -webkit-clip-path: polygon(
        calc(0% + #{$terminal-cards-deg}) 0,
        100% 0%,
        100% 100%,
        0% 100%
      );
      clip-path: polygon(
        calc(0% + #{$terminal-cards-deg}) 0,
        100% 0%,
        100% 100%,
        0% 100%
      );
      box-shadow: none;
    }
  }

  &__box {
    padding: 20px 40px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: flex-start;
    height: 100%;
    background-color: #FFFFFF;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, .1);

    &:before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 80px;
      height: 30px;
      background-color: #EA252C;
      transform: skewX(#{$number-card-deg-before});
    }
  }

  &__icon {
    min-height: 100px;
    display: flex;
    align-items: flex-end;
    align-content: flex-end;
    flex-direction: column;
    justify-content: flex-end;
    margin-bottom: 25px;
    text-align: center;

    img {
      vertical-align: bottom;
    }
  }

  &__info {
    font-weight: 300;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #222B34;
  }
}


@media (max-width: 992px) {
  .advantage-terminal {
    &__wrap {
      padding-left: 30px;
      overflow: hidden;
    }
    .advantage-card__box {
      height: 340px;
    }
    .owl-dots.disabled,
    .owl-nav.disabled {
      display: none;
    }
    .owl-stage {
      padding-left: 30px;
    }
    .owl-stage-outer {
      max-width: 100%;
      padding-top: 50px;
      padding-bottom: 30px;
      margin-top: -10px;
      margin-bottom: -30px;

      overflow: hidden;
    }
  }

  .terminal-cards {
    height: 460px;
    min-height: 460px;
    &__bg {
      -webkit-clip-path: polygon(0 calc(0% + 30px), 50% calc(0% + 30px), 100% 0, 100% calc(100% - 65px), 50% calc(100% - 65px), 0 100%);
      clip-path: polygon(0 calc(0% + 30px), 50% calc(0% + 30px), 100% 0, 100% calc(100% - 65px), 50% calc(100% - 65px), 0 100%);
    }
    &__wrap {
      display: block;
      padding-top: 80px;
    }
  }

  .terminal-cards__cards .terminal-card {
    min-width: 407px;
    max-width: 407px;
  }
  .terminal-cards__cards .owl-item:nth-child(1) .terminal-card__box {
    -webkit-clip-path: polygon(0 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    clip-path: polygon(0 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
  }
  .terminal-cards__cards .owl-item:nth-child(2) .terminal-card__box {
    -webkit-clip-path: polygon(calc(0% + 25px) 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    clip-path: polygon(calc(0% + 25px) 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    .terminal-card__header-icon {
      margin-left: 16px;
    }
  }
  .terminal-cards__cards .owl-item:nth-child(3) .terminal-card__box {
    -webkit-clip-path: polygon(calc(0% + 25px) 0, 100% 0%, 100% 100%, 0% 100%);
    clip-path: polygon(calc(0% + 25px) 0, 100% 0%, 100% 100%, 0% 100%);
    .terminal-card__header-icon {
      margin-left: 16px;
    }
  }

  .terminal-card__header .terminal-card__header-title {
    padding-right: 20px;
  }

  .terminal-cards__cards {
    .owl-dots.disabled,
    .owl-nav.disabled {
      display: none;
    }
    .owl-stage {
      padding-left: 30px;
    }
    .owl-stage-outer {
      max-width: 100%;
      padding-top: 30px;
      padding-bottom: 30px;
      margin-top: -30px;
      margin-bottom: -30px;

      overflow: hidden;
    }
  }
}

@media (max-width: 678px) {

  .text-terminal .icon-cards {
    margin: 0;
  }

  .text-terminal {
    overflow: hidden;
    &__wrap {
      display: block;
      max-width: 100%;
    }
    .owl-stage-outer {
      padding-top: 10px;
      margin-top: -10px;
      padding-bottom: 30px;
      .owl-stage {
        display: flex;
      }
    }
    .owl-nav.disabled {
      display: none;
    }
    .icon-cards {
      padding-bottom: 0;
    }
    .icon-card__box {
      flex-wrap: wrap;
      min-width: 170px;
      .icon-card__icon {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 30px;
        &:before {
          top: 20px;
        }
      }
    }
    .icon-card__content {
      padding-right: 0;
      text-align: center;
      padding-left: 5px;
      padding-right: 5px;
      flex-wrap: wrap;
      justify-content: center;
      .icon-card__content-number {
        font-size: 50px;
        line-height: 1;
        margin-right: 0;
      }
    }
  }
  .text-terminal .icon-card {
    flex: 1 1 100%;
    flex-basis: 100%;
    max-width: 100%;
    min-width: 100%;
    margin-bottom: 0;
    .icon-card__content-info,
    .icon-card__content-title {
      font-size: 14px;
      line-height: 1.4;
    }
    .icon-card__content p {
      font-size: 14px;
      line-height: 1.4;
    }
  }
}
