$partners-start-height:   50px;
$partners-start-bottom:   15px;
$partners-start-deg:      20px;

.partners-start {
  position: relative;
  min-height: 600px;
  height: auto;

  &:before {
    position: absolute;
    content: '';
    height: $partners-start-height * 2;
    width: 100%;
    background-color: #ffffff;
    left: 0;
    top: -1px;
    z-index: 0;
  }

  &__bg {
    background-color: #F3F4F5;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    -webkit-clip-path: polygon(
      0    calc(0% + #{$partners-start-height}),
      50%  calc(0% + #{$partners-start-height}),
      100% 0,
      100% calc(100% - #{$partners-start-height + $partners-start-bottom}),
      50%  calc(100% - #{$partners-start-height + $partners-start-bottom}),
      0    100%
    );
    clip-path: polygon(
      0    calc(0% + #{$partners-start-height}),
      50%  calc(0% + #{$partners-start-height}),
      100% 0,
      100% calc(100% - #{$partners-start-height + $partners-start-bottom}),
      50%  calc(100% - #{$partners-start-height + $partners-start-bottom}),
      0    100%
    );
    /*
      1---2---3
      \-------\
      6---5---4
    */
  }

  &__wrap {
    height: 100%;
    max-width: 1180px;
    margin: 0 auto;
    padding-top: $partners-start-height;
    padding-bottom: $partners-start-height + $partners-start-bottom;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    z-index: 1;
    position: relative;
  }

  .intro-box {
    padding-top: 100px;
    padding-bottom: 40px;
  }
  @media only screen and (max-width: 1024px) {
    .icon-card__content {
      min-width: 100%;
    }
  }
  &__cards {
    @media only screen and (min-width: 1024px) {

    .icon-cards {
      justify-content: space-between;
    }
    .icon-card:nth-child(3n+1) {
      flex: 1 1 35%;
      flex-basis: 35%;
      max-width: 35%;
      min-width: 35%;
    }
    .icon-card:nth-child(3n+2) {
      flex: 1 1 34%;
      flex-basis: 34%;
      max-width: 34%;
      min-width: 34%;
    }
    .icon-card:nth-child(3n+1) {
      flex: 1 1 35%;
      flex-basis: 35%;
      max-width: 35%;
      min-width: 35%;
    }
    .icon-card:nth-child(3n+3) {
      flex: 1 1 29%;
      flex-basis: 29%;
      max-width: 29%;
      min-width: 29%;
      .icon-card__content {
        padding-right: 0;
      }
    }
  }
  }
}


@media #{$tablet_gt} {
  .partners-start {
    min-height: 470px;
    overflow: hidden;
    &__wrap {
      display: block;
      max-width: 100%;
    }
    .intro-box {
      padding-top: 80px;
    }
    .owl-stage-outer {
      padding-top: 10px;
      margin-top: -10px;
      padding-bottom: 30px;
      .owl-stage {
        display: flex;
      }
    }
    .owl-nav.disabled {
      display: none;
    }
    .icon-cards {
      padding-bottom: 0;
    }
    .icon-card__box {
      flex-wrap: wrap;
      min-width: 190px;
      .icon-card__icon {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 30px;
        &:before {
          top: 20px;
        }
      }
    }
    .icon-card__content {
      padding-right: 0;
      text-align: center;
      padding-left: 25px;
      padding-right: 25px;
    }
  }
}

@media (max-width: 678px) {
  .partners-start__wrap {
    padding-top: 0;
  }
  .partners-start__bg {
    -webkit-clip-path: polygon(0 calc(0% + 10px), 50% calc(0% + 10px), 100% 0, 100% calc(100% - 10px), 50% calc(100% - 10px), 0 100%);
    clip-path: polygon(0 calc(0% + 10px), 50% calc(0% + 10px), 100% 0, 100% calc(100% - 10px), 50% calc(100% - 10px), 0 100%);
  }
  .partners-start .icon-cards {
    margin: 0;
  }
  .icon-cards--three .icon-card {
    flex: 1 1 100%;
    flex-basis: 100%;
    max-width: 100%;
    min-width: 100%;
    margin-bottom: 0;
    .icon-card__content p {
      line-height: 1.4;
    }
  }
}
