$text-safety-deg:  30px;

.text-safety {

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;
    padding-top: 80px;
    padding-bottom: 100px;
    position: relative;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  &__notchoose {
    justify-content: space-between;
    padding-bottom: 100px;

    &-text {
      flex-basis: 680px;
      min-width: 680px;
      margin-right: 50px;
    }

    &-pic {
      position: relative;
      padding-right: 100px;

      &:after {
        position: absolute;
        content: '';
        background-color: #F3F4F5;
        -webkit-clip-path: polygon(
          0 calc(0% + #{$text-safety-deg}),
          100% 0,
          100% 100%,
          0% 100%
        );
        clip-path: polygon(
          0 calc(0% + #{$text-safety-deg}),
          100% 0,
          100% 100%,
          0% 100%
        );
        width: 350px;
        height: 320px;
        right: 30px;
        bottom: -24px;
        z-index: -1;
      }
    }

    &-img {
      background-color: #232D3A;
      -webkit-clip-path: polygon(
        0 calc(0% + #{$text-safety-deg}),
        100% 0,
        100% 100%,
        0% 100%
      );
      clip-path: polygon(
        0 calc(0% + #{$text-safety-deg}),
        100% 0,
        100% 100%,
        0% 100%
      );
      width: 350px;
      height: 320px;
      position: relative;
      z-index: 0;

      img {
        width: 100%;
        vertical-align: middle;
        object-fit: cover;
        position: relative;
      }
    }
  }

  &__cards {
    padding-bottom: 40px;
  }
}

@media (max-width: 1176px) {
  .text-safety__notchoose-text {
    flex-basis: 50%;
    min-width: 50%;
  }
  .text-safety__notchoose-pic {
    padding-right: 60px;
    &:after {
      right: 0;
    }
  }
  .text-safety__cards {
    flex-wrap: nowrap;
    .owl-stage-outer {
      max-width: 100%;
      overflow: hidden;
      padding-left: 20px;
      padding-bottom: 30px;
    }
    .owl-stage {
      display: flex;
    }
    .owl-nav.disabled {
      display: none;
    }
    .owl-item {
      &:nth-child(odd) .flag-card {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% calc(100% - 40px), 0 100%);
        clip-path: polygon(0 0, 100% 0, 100% calc(100% - 40px), 0 100%);
      }
      &:nth-child(even) .flag-card {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 40px));
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 40px));
      }
      &:nth-child(1) {
        order: 1;
        .flag-card {
          -webkit-clip-path: polygon(0 0, 100% 0, 100% calc(100% - 40px), 0 100%);
          clip-path: polygon(0 0, 100% 0, 100% calc(100% - 40px), 0 100%);
        }
      }
      &:nth-child(2) {
        order: 2;
      }
      &:nth-child(3) {
        order: 3;
      }
      &:nth-child(4) {
        order: 6;
      }
      &:nth-child(5) {
        order: 4;
        .flag-card {
          -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 40px));
          clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 40px));
        }
      }
      &:nth-child(6) {
        order: 5;
        .flag-card {
          -webkit-clip-path: polygon(0 0, 100% 0, 100% calc(100% - 40px), 0 100%);
          clip-path: polygon(0 0, 100% 0, 100% calc(100% - 40px), 0 100%);
        }
      }

    }
    .flag-card {
      min-height: 480px;
      max-width: 270px;
      min-width: 270px;
      margin-right: 0;
      .flag-card__box {
        padding-left: 20px;
        padding-right: 20px;
        padding-bottom: 20px;
      }
      .flag-card__headline {
        margin-bottom: 15px;
        min-height: 80px;
        &:after {
          bottom: -3px;
        }
        h3.h4 {
          font-size: 18px;
          line-height: 1.4;
        }
      }
      .flag-card__body p {
        font-size: 14px;
        line-height: 1.6;
      }
      .flag-card__icon-box {
        width: 60px;
        height: 60px;
        svg {
          max-width: 40px;
        }
      }
    }
  }
}


@media (max-width: 1100px) {
  .headline.headline--safety .headline-flag:after {
    left: 50%;
    margin-left: -315px;
    width: 75px;
    height: 40px;
  }
  .text-safety__wrap {
    .text-safety__notchoose {
      max-width: 630px;
      margin-left: auto;
      margin-right: auto;
    }
    .intro-box.p-decor .h2 {
      width: 630px;
      font-size: 30px;
      line-height: 1.4;
    }
    .intro-box.p-decor p {
      font-size: 16px;
      line-height: 1.6;
    }
  }
  .text-safety__notchoose-text {
      flex-basis: 400px;
      min-width: 400px;
  }
  .text-safety__notchoose-pic {
    margin-top: 90px;
    padding-right: 30px;
    &:after {
      right: 0;
      width: 200px;
      height: 180px;
    }
    .text-safety__notchoose-img {
      width: 200px;
      height: 180px;
    }
  }

  .flag-cards {
    margin-right: 0;
    margin-left: 0;
    width: 100%;
  }

}

@media only screen and (max-width: 767px) and (min-width: 576px) {
  .text-safety__wrap {
    max-width: 100%;
  }
}
@media (max-width: 662px) {
  .text-safety__wrap .intro-box.p-decor .h2 {
    width: 100%;
    font-size: 20px;
    line-height: 1.4;
  }
  .text-safety__wrap .intro-box.p-decor p {
    font-size: 14px;
    &::before {
      width: 30px;
    }
  }
  .text-safety__notchoose-pic {
    display: none;
  }
}

@media (max-width: 618px) {
  .headline.headline--safety .headline-flag:after {
    left: 0;
    margin-left: 0;
    width: 50px;
  }
  .text-safety__wrap {
      max-width: 90%;
      min-width: 320px;
      padding-bottom: 10px;
  }
  .text-safety__wrap .text-safety__notchoose {
    padding-bottom: 40px;
  }
  .text-safety__notchoose-text {
      flex-basis: 100%;
      min-width: 100%;
  }
  .text-safety__wrap .intro-box.p-decor p {
    padding-left: 0;
    &::before {
      width: 20px;
      margin-top: -25px;
    }
  }
  .text-safety__cards .owl-stage-outer {
    padding-left: 0;
  }
  .text-safety__cards .flag-card .flag-card__box {
    padding-top: 30px;
  }
}
