.start-trading {
  padding-top: 100px;
  @media #{$tablet_lt} {
    padding-top: 50px;
  }
  &__wrap {
    min-width: 1180px;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
    @media #{$tablet} {
      min-width: 100%;
    }
    @media #{$tablet} {
      margin-left: auto;
      margin-right: 0;
      .owl-nav.disabled {
        display: none;
      }
      .owl-stage-outer {
        margin-top: -30px;
        padding-top: 50px;
        padding-bottom: 30px;
        max-width: calc(100% - 60px);
        margin-left: auto;
        margin-right: 0;
        .advantage-card {
          margin-left: 20px;
          margin-right: auto;
        }
      }
    }
    @media #{$tablet_lt} {
      margin-left: auto;
      margin-right: 0;
      padding-left: 0;
      padding-right: 0;
      .owl-nav.disabled {
        display: none;
      }
      .owl-stage-outer {
        margin-top: -30px;
        padding-top: 50px;
        padding-bottom: 30px;
        padding-left: 20px;
        max-width: 100%;
        margin-left: auto;
        margin-right: 0;
        .advantage-card {
          margin-left: 20px;
          margin-right: auto;
        }
      }
    }
  }
  @media #{$tablet_lt} {
    .intro-box.slash {
      h2 {
        margin-bottom: 40px;
        &:after {
          width: 20px;
          bottom: -25px;
        }
        span {
          display: block;
        }
      }
      p {
        font-size: 16px;
        line-height: 1.4;
        padding-left: 5%;
        padding-right: 5%;
      }
    }
  }
  &__cards {
    padding-top: 120px;
    padding-bottom: 60px;
  }
}

.advantage-cards--shadow {
  @media #{$tablet} {
    display: block;
    .owl-stage {
      display: flex;
    }
    .owl-dots,
    .disabled {
      display: none;
    }
  }
  @media #{$tablet_lt} {
    display: block;
    .owl-stage {
      display: flex;
    }
    .owl-dots,
    .disabled {
      display: none;
    }
    .advantage-card {
      &__name {
        font-size: 20px;
      }
      &__body {
        &,
        & p {
          font-size: 14px;
          line-height: 1.6;
        }
      }
    }
  }
}
