.text-about {
  padding-top: 70px;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  &__quote {
    // sass-lint:disable-block indentation
    margin-top: 100px;
    background:
      (linear-gradient( 100deg, #F3F4F5 450px, transparent 451px)),
      (linear-gradient( 0deg, #EA252C 100%, transparent 50.1%));
    height: 300px;

    @media #{$mobile} {
      flex-direction: column;
      height: auto;
      background:
        (linear-gradient( bottom, #F3F4F5 450px, transparent 451px)),
        (linear-gradient( 0deg, #EA252C 100%, transparent 50.1%));
    }
    @media #{$mobile-big} {
    }
    @media #{$tablet} {
    }

    &-photo {
      width: 450px;
      flex-basis: 450px;
      min-width: 450px;
      position: relative;
      height: 100%;
      padding: 30px;

      img {
        vertical-align: middle;
        position: absolute;
        bottom: 30px;
        -webkit-clip-path: polygon(
          0 0,
          100% 0%,
          calc(100% - 60px) 100%,
          0% 100%
        );
        clip-path: polygon(
          0 0,
          100% 0%,
          calc(100% - 60px) 100%,
          0% 100%
        );
      }
    }

    &-text {
      flex: 1;
      padding: 50px 50px 50px 90px;
    }

    .blockquote p {
      font-weight: 500;
      color: #ffffff;
    }
  }

  .caption-row {
    padding-top: 100px;
    padding-bottom: 60px;
    display: flex;
    align-items: center;
    align-content: center;
    font-family: 'Roboto';

    @media #{$mobile} {
      flex-direction: column;
    }
    @media #{$mobile-big} {
    }
    @media #{$tablet} {
    }

    &__title {
      width: 340px;
      text-align: right;
      margin-right: 50px;

      .h2 {
        color: #6E7A86;
        font-size: 36px;
        line-height: 47px;
        margin: 0;
      }
    }

    &__text {
      flex: 1;
      padding-right: 20px;

      p {
        font-size: 18px;
        line-height: 29px;
        color: #222B34;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .text-about__wrap {
    max-width: 90%;
    .text-content p {
      font-size: 16px;
      line-height: 1.6;
    }
    .text-about__quote {
      background:
        linear-gradient(100deg, #F3F4F5 37%, transparent 37.1%),
        linear-gradient(0deg, #EA252C 100%, transparent 50.1%);
    }
    .text-about__quote-photo {
      width: 33%;
      min-width: 33%;
      flex-basis: 33%;
      height: 270px;
      min-height: 270px;
      object-fit: cover;
      img {
        min-width: 100%;
        height: 270px;
        object-fit: cover;
        object-position: 70%;

        -webkit-clip-path: polygon(0 0, 100% 0%, calc(100% - 50px) 100%, 0% 100%);
        clip-path: polygon(0 0, 100% 0%, calc(100% - 50px) 100%, 0% 100%);
      }
    }
    .blockquote {
      font-size: 16px;
      line-height: 1.6;
    }
    .text-about__quote-text {
      padding-left: 15%;
      &:before {
        left: 0;
      }
    }
  }

  .text-about .icon-cards {
    margin-left: 0;
    margin-right: 0;
    .icon-card__content {
      padding-right: 20px;
    }
  }
  .text-about .caption-row {
    padding-top: 70px;
    .caption-row__title {
      width: 35%;
      min-width: 250px;
      margin-right: 5%;
      font-size: 30px;
      line-height: 1.4;
    }
    .caption-row__text {
      padding-right: 0;
    }
  }
}
@media (max-width: 724px) {
  .text-about .icon-cards {
    padding-left: 5%;
    .icon-card {
      width: 100%;
      flex-basis: 100%;
      flex: 1 1 100%;
      margin-bottom: 0;
    }
    .owl-nav.disabled {
      display: none;
    }
    .owl-stage-outer {
      min-width: 100%;
      overflow: hidden;
      padding-top: 10px;
      margin-top: -10px;
      padding-bottom: 30px;
      margin-bottom: -30px;
      padding-left: 20px;
      .owl-stage {
        display: flex;
      }
    }
    .icon-card__box {
      flex-wrap: wrap;
      min-width: 150px;
      align-items: center;
      text-align: center;
      .icon-card__icon {
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 45px;
        &::before {
          top: 25px;
        }
      }
      .icon-card__content {
        padding-right: 0;
      }
    }
  }
}
@media (max-width: 678px) {
  .text-about .text-about__wrap {
    max-width: 90%;
    .caption-row__title h2 {
      margin-left: 5%;
      margin-right: 5%;
    }
    .text-content p {
      margin-left: 5%;
      margin-right: 5%;
      font-size: 16px;
      line-height: 1.6;
    }
    .text-about__quote {
      background:
        linear-gradient(173deg, #F3F4F5 37%, transparent 37.1%),
        linear-gradient(0deg, #EA252C 100%, transparent 50.1%);
    }
    .text-about__quote-photo {
      width: 90%;
      min-width: 300px;
      max-width: 300px;
    }
  }
}
@media (max-width: 656px) {
  .text-about .text-about__wrap {
    max-width: 100% !important;
    .text-content p {
      padding-left: 20px;
      font-size: 16px;
      line-height: 1.6;
    }
    .text-about__quote-photo {
      width: 100%;
      min-width: 100%;
      flex-basis: 100%;
      height: 270px;
      min-height: 270px;
      object-fit: cover;
      img {
        min-width: 245px;
        height: 270px;
        object-fit: cover;
        object-position: 70%;

        left: 50%;
        transform: translateX(-50%);

        -webkit-clip-path: polygon(0 0, 100% 0%, 100% calc(100% - 35px), 0% 100%);
        clip-path: polygon(0 0, 100% 0%, 100% calc(100% - 35px), 0% 100%);
      }
    }
    .text-about__quote {
      height: auto;
      flex-wrap: wrap;
      background:
        linear-gradient(173deg, #F3F4F5 46%, transparent 46.1%),
        linear-gradient(0deg, #EA252C 100%, transparent 360px);
    }
    .text-about__quote-text {
      width: 100%;
      padding-left: 15%;
      .blockquote:before {
        top: -40px !important;
        left: -5px !important;
      }
    }
    .blockquote {
      font-size: 16px;
      line-height: 1.6;
    }

    .caption-row {
      padding-top: 50px;
      padding-left: 20px;
      flex-wrap: wrap;
      .caption-row__title {
        width: 100%;
        text-align: left;
        margin-right: 0;
        .h2 {
          margin-bottom: 20px;
          font-size: 20px;
          line-height: 1.4;
        }
      }
      .caption-row__text {
        width: 100%;
        padding-right: 0;
        p {
          font-size: 14px;
          line-height: 1.8;
        }
      }
    }
  }

}

@media (max-width: 1100px) and (min-width: 768px) {
  .text-about {
    .text-content p {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  .double-background__caption {
    padding-left: 50px;
    padding-right: 130px;
  }
  .double-background__info {
    padding-right: 20px;
  }
}
