$best-traders-cards-deg:   20px;

.best-traders {
  padding-top: 60px;
  padding-bottom: 90px;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  &__cards {
    margin-top: 50px;
    margin-bottom: 50px;

    .number-card {
      height: 270px;

      &:nth-child(1) .number-card__box {
        -webkit-clip-path: polygon(
          0 0,
          100% 0%,
          calc(100% - #{$best-traders-cards-deg}) 100%,
          0% 100%
        );
        clip-path: polygon(
          0 0,
          100% 0%,
          calc(100% - #{$best-traders-cards-deg}) 100%,
          0% 100%
        );
      }

      &:nth-child(2) .number-card__box {
        -webkit-clip-path: polygon(
          calc(0% + #{$best-traders-cards-deg}) 0,
          100% 0%,
          calc(100% - #{$best-traders-cards-deg}) 100%,
          0% 100%
        );
        clip-path: polygon(
          calc(0% + #{$best-traders-cards-deg}) 0,
          100% 0%,
          calc(100% - #{$best-traders-cards-deg}) 100%,
          0% 100%
        );
      }

      &:nth-child(3) .number-card__box {
        -webkit-clip-path: polygon(
          calc(0% + #{$best-traders-cards-deg}) 0,
          100% 0%,
          100% 100%,
          0% 100%
        );
        clip-path: polygon(
          calc(0% + #{$best-traders-cards-deg}) 0,
          100% 0%,
          100% 100%,
          0% 100%
        );
      }

      &__number {
        left: 40px;
        font-size: 200px;
        line-height: 200px;
      }

      &__content {
        display: flex;
        align-items: flex-start;
        align-content: flex-start;
        flex-direction: column;
        padding-left: 80px;
        padding-right: 0;
      }
    }
  }
}


@media #{$tablet_part} {
  .best-traders {
    height: 600px;
    min-height: 600px;
    padding-top: 82px;
  }
  .best-traders__wrap {
    max-width: 100%;
    overflow: hidden;
    display: block;
  }
  .best-traders .number-card {
    min-width: 407px;
    max-width: 407px;
  }
  .best-traders .owl-item:nth-child(1) .number-card__box {
    -webkit-clip-path: polygon(0 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    clip-path: polygon(0 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
  }
  .best-traders .owl-item:nth-child(2) .number-card__box {
    -webkit-clip-path: polygon(calc(0% + 25px) 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    clip-path: polygon(calc(0% + 25px) 0, 100% 0%, calc(100% - 25px) 100%, 0% 100%);
    .number-card__header-icon {
      margin-left: 16px;
    }
  }
  .best-traders .owl-item:nth-child(3) .number-card__box {
    -webkit-clip-path: polygon(calc(0% + 25px) 0, 100% 0%, 100% 100%, 0% 100%);
    clip-path: polygon(calc(0% + 25px) 0, 100% 0%, 100% 100%, 0% 100%);
    .number-card__header-icon {
      margin-left: 16px;
    }
  }

  .number-card__header .number-card__header-title {
    padding-right: 20px;
  }

  .best-traders__cards {
    .owl-dots.disabled,
    .owl-nav.disabled {
      display: none;
    }
    .owl-stage {
      padding-left: 30px;
    }
    .owl-stage-outer {
      max-width: 100%;
      padding-top: 30px;
      padding-bottom: 30px;
      margin-top: -30px;
      margin-bottom: -30px;

      overflow: hidden;
    }
  }
}
@media (max-width: 570px) {
  .h2 > span {
    display: block;
  }
  .best-traders .number-card:nth-child(1) .number-card__box .number-card__header-icon {
    width: 70px;
    height: 60px;
    svg use {
      width: 40px;
      height: 40px;
    }
  }
  .best-traders .number-card__header {
    height: 60px;
  }
  .best-traders .number-card__header-title {
    font-size: 16px;
  }
}
@media (max-width: 420px) {
  .best-traders .number-card {
      min-width: 300px;
      max-width: 300px;
  }
  .best-traders .number-card__content {
    padding-left: 45px;
    position: relative;
  }
  .number-cards .owl-item:nth-child(3),
  .number-cards .owl-item:nth-child(2) {
    .number-card__header-icon {
      align-self: start;
    }
    .number-card__header {
      height: auto;
    }
  }
  .best-traders {
    padding-bottom: 0;
    height: auto;
    min-height: unset;
  }
}
