$main-features-height:   110px;
$main-features-bottom:   15px;
$main-features-deg:      20px;

.main-features {
  position: relative;
  height: 550px;
  @media #{$tablet} {
    height: 518px;
  }
  @media #{$tablet_lt} {
    height: 380px;
  }


  &:before {
    position: absolute;
    content: '';
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
  }

  &__bg {
    background-color: #EA252C;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    -webkit-clip-path: polygon(
      0    calc(0% + #{$main-features-height}),
      50%  calc(0% + #{$main-features-height}),
      100% 0,
      100% calc(100% - #{$main-features-height + $main-features-bottom}),
      50%  calc(100% - #{$main-features-height + $main-features-bottom}),
      0    100%
    );
    clip-path: polygon(
      0    calc(0% + #{$main-features-height}),
      50%  calc(0% + #{$main-features-height}),
      100% 0,
      100% calc(100% - #{$main-features-height + $main-features-bottom}),
      50%  calc(100% - #{$main-features-height + $main-features-bottom}),
      0    100%
    );

    @media #{$tablet} {
      -webkit-clip-path: polygon(
        0    calc(0% + 60px),
        50%  calc(0% + 60px),
        100% 0,
        100% calc(100% - 60px),
        50%  calc(100% - 60px),
        0    100%
      );
      clip-path: polygon(
        0    calc(0% + 60px),
        50%  calc(0% + 60px),
        100% 0,
        100% calc(100% - 60px),
        50%  calc(100% - 60px),
        0    100%
      );
    }

    @media #{$tablet_lt} {
      -webkit-clip-path: polygon(
        0    calc(0% + 30px),
        50%  calc(0% + 30px),
        100% 0,
        100% calc(100% - 30px),
        50%  calc(100% - 30px),
        0    100%
      );
      clip-path: polygon(
        0    calc(0% + 30px),
        50%  calc(0% + 30px),
        100% 0,
        100% calc(100% - 30px),
        50%  calc(100% - 30px),
        0    100%
      );
    }
    /*
      1---2---3
      \-------\
      6---5---4
    */
  }

  &__wrap {
    height: 100%;
    max-width: 1180px;
    margin: 0 auto;
    padding-top: $main-features-height;
    padding-bottom: $main-features-height + $main-features-bottom;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    z-index: 1;
    position: relative;
    @media #{$tablet} {
      padding-top: 130px;
      padding-bottom: 45px;
    }
    @media #{$tablet_lt} {
      padding-bottom: 50px;
      .owl-nav.disabled {
        display: none;
      }
      .owl-stage-outer {
        padding-top: 10px;
        padding-bottom: 50px;
        padding-left: 20px;
        max-width: 100%;
        margin-left: auto;
        margin-right: 0;
        .one-click__item:nth-child(1) {
          top: 0;
        }
      }
      .icon-card {
        .icon-card__box {
          min-width: 180px;
          flex-wrap: wrap;
          .icon-card__icon {
            margin-left: auto;
            margin-right: auto;
            &:before {
              top: 25px;
            }
          }
          .icon-card__content {
            top: 50px;
            padding-right: 0;
            text-align: center;
            width: 100%;
          }
        }
      }
    }
  }

  &__cards {
    padding-bottom: 50px;
  }
}
