.business-model {
  padding-bottom: 80px;
  font-family: 'Roboto';

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;
    position: relative;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  &__bg {
    // sass-lint:disable-block indentation
    background:
      linear-gradient( 105deg, #EA252C 500px, transparent 501px),
      linear-gradient( 0deg, #F3F4F5 100%, transparent 50.1%);
    height: 350px;

    @media #{$mobile} {
      flex-direction: column;
      height: auto;
    }
    @media #{$mobile-big} {
    }
    @media #{$tablet} {
    }
  }

  &__quote {
    height: 100%;
    width: 500px;
    max-width: 500px;
    flex-basis: 500px;
    padding: 50px 50px 50px 90px;
  }

  &__info {
    flex: 1;
    font-weight: 300;
    font-size: 18px;
    line-height: 29px;
    color: #222B34;
    padding-left: 40px;
    padding-right: 40px;

    p {

      &:last-child {
        margin-bottom: 0;
      }

      strong {
        font-weight: bold;
      }
    }
  }
}

.blockquote {
  position: relative;
  font-family: 'Roboto';
  color: #FFFFFF;
  font-weight: 500;
  font-size: 18px;
  line-height: 29px;
  margin-bottom: 0;

  &:before {
    position: absolute;
    content: '';
    background-image: url('data:image/svg+xml,%3Csvg width="17" height="13" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M14.256.728c.224 1.664.56 3.584 1.008 5.76.48 2.144.944 4.144 1.392 6h-3.12c-.8-1.728-1.6-3.6-2.4-5.616-.768-2.048-1.36-3.92-1.776-5.616l.336-.528h4.56zm-8.88 0a117.88 117.88 0 0 0 1.056 5.76c.448 2.144.896 4.144 1.344 6H4.704c-.8-1.728-1.584-3.6-2.352-5.616-.736-2.048-1.328-3.92-1.776-5.616L.864.728h4.512z" fill="%23fff"/%3E%3C/svg%3E');
    width: 17px;
    height: 13px;
    top: -10px;
    left: -45px;
  }

  p {
    margin-bottom: 0;
    color: #FFFFFF;
    font-weight: 500;
  }

  footer {
    margin-top: 20px;
  }

  cite {
    font-weight: 300;
    font-style: italic;
  }
}



@media (max-width: 970px) {
  .business-model {
    .intro-box.text-left .h2 {
      font-size: 30px;
      text-align: center;
    }
  }
  .business-model__wrap {
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  .business-model__bg {
    height: unset;
    padding-top: 50px;
    padding-bottom: 50px;
    background: linear-gradient(105deg, #EA252C 46%, transparent 46.1%), linear-gradient(0deg, #F3F4F5 100%, transparent 50.1%);
  }
  .business-model {
    .business-model__quote {
      max-width: 50%;
      padding-right: 7%;
      padding-left: 5%;
      .blockquote:before {
        left: 0;
        top: -40px;
      }
    }
    .business-model__info {
      padding-left: 5%;
    }
    .h2 {
      font-size: 20px;
      line-height: 1.8;
    }
    p {
      font-size: 16px;
      line-height: 1.8;
    }
  }
}

@media (max-width: 656px) {
  .business-model__bg {
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
    flex-wrap: wrap;
    .col-half {
      flex-basis: unset;
    }
  }
  .business-model__bg {
    background: linear-gradient(173deg, #EA252C 43%, transparent 43.1%), linear-gradient(0deg, #F3F4F5 100%, transparent 50.1%);
    height: auto;
    align-items: start;
    align-content: start;
    padding-bottom: 0;
    .business-model__quote {
      min-width: 100%;
      padding: 5%;
      width: auto;
      height: auto;
      flex-basis: auto;
    }
    .business-model__info {
      min-width: 100%;
      padding: 5%;
    }
    .h2 {
      max-width: 265px;
      font-size: 16px;
      line-height: 1.8;
      &:before {
        width: 30px;
        left: -28px;
      }
    }
    p {
      font-size: 14px;
      line-height: 1.8;
    }
  }
}

@media (max-width: 532px) {
  .business-model__bg {
    max-width: 100%;
    background: linear-gradient(173deg, #EA252C 48%, transparent 48.1%), linear-gradient(0deg, #F3F4F5 100%, transparent 50.1%);
    height: auto;
    align-items: start;
    align-content: start;
    padding-bottom: 0;
    .business-model__quote {
      min-width: 100%;
      padding: 7%;
      width: auto;
      height: auto;
      flex-basis: auto;
    }
    .business-model__info {
      min-width: 100%;
      padding: 10% 5% 7%;
    }
    p {
      font-size: 14px;
      line-height: 1.8;
    }
  }
}

@media (max-width: 414px) {
  .business-model__bg {
    background: linear-gradient(173deg, #EA252C 43%, transparent 43.1%), linear-gradient(0deg, #F3F4F5 100%, transparent 50.1%);
  }
}
