.text-terminal {
  padding-top: 80px;
  padding-bottom: 80px;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  .intro-box {
    padding-left: 100px;
    padding-right: 100px;
  }

  .icon-cards {
    padding-top: 60px;
    padding-bottom: 0;
  }
}
@media (max-width: 768px) and (min-width: 690px) {
  .text-terminal {
    .icon-card__content-number {
      font-size: 70px;
      line-height: 1;
    }
    .icon-card__content-title {
      font-size: 18px;
      line-height: 1.4;
    }
    .icon-card:nth-child(3) .icon-card__content,
    .icon-card:nth-child(4) .icon-card__content {
      align-items: flex-start;
      .icon-card__content-description {
        margin-top: 5px;
      }
      .icon-card__content-info {
        padding-top: 10px;
        margin-left: -50px;
      }
    }
  }
}
@media (max-width: 690px) {
  .text-terminal {
    padding-bottom: 20px;
  }
  .text-terminal .intro-box{
    padding-left: 20px;
    padding-right: 20px;
  }
}
