.partners-reason {
  padding-top: 50px;
  padding-bottom: 120px;

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
    }
  }

  .intro-box {

    .h2 span {
      display: block;
    }
  }

  &__list {
    clear: both;
    overflow: auto;
    padding-top: 10px;
  }
}

@media #{$tablet_gt} {
  .partners-reason {
    &__wrap {
      max-width: $max-width-tablet;
    }
    .plan-list__info {
      font-size: 16px;
      line-height: 1.4;
    }
  }
}

@media (max-width: 678px) {
  .partners-reason .partners-reason__list {
    overflow: unset;
  }
  .partners-reason .plan-list__item .plan-list__box {
    padding-left: 0;
    padding-right: 0;
    position: relative;
    &::after,
    &::before {
      display: none;
    }
  }

  .plan-list--arrow .plan-list__item:nth-child(even) .plan-list__content {
    padding-right: 10px;
    margin-right: 20px;
    border-left: 0;
    &::before {
      // right: -10px;
    }
  }
  .plan-list--arrow .plan-list__item:nth-child(odd) .plan-list__content {
    padding-left: 10px;
    margin-left: 20px;
    word-break: break-all;
    border-right: 0;
    &::before {
      // left: -10px;
    }
  }
  .plan-list--arrow .plan-list__item:nth-child(odd) .plan-list__point {
    margin-right: 5px;
    left: 10px;
  }
  .plan-list--arrow .plan-list__item:nth-child(even) .plan-list__point {
    margin-left: 5px;
    right: 10px;
  }
  .plan-list--arrow .plan-list__item:nth-child(even) .plan-list__info,
  .plan-list--arrow .plan-list__item:nth-child(odd) .plan-list__info {
    position: relative;
  }
  .plan-list--arrow .plan-list__item:nth-child(even) .plan-list__point,
  .plan-list--arrow .plan-list__item:nth-child(odd) .plan-list__point {
    position: absolute;
  }
}
