.headline {
  background-color: #F3F4F5;
  background: linear-gradient(
    113deg,
    #F3F4F5 0%,
    #F3F4F5 49%,
    #EA252C 49.1%,
    #EA252C 69%,
    #232D3A 69.1%,
    #232D3A 69%,
  );
  height: 450px;
  @media #{$tablet} {
    height: 730px;
  }

  &:after {
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__wrap {
    max-width: 1180px;
    margin: 0 auto;
    padding-top: 0;
    padding-bottom: 50px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-content: flex-start;
    justify-content: center;
    z-index: 1;
    position: relative;

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$tablet} {
      max-width: $max-width-tablet;
    }
    @media #{$tablet} {
      max-width: $max-width-tablet;
    }
  }

  &__content {
    max-width: 440px;

    p {
      position: relative;
      padding-left: 40px;
      font-family: 'Roboto';
      font-size: 18px;
      line-height: 29px;
      color: #232D3A;

      &:before {
        content: '';
        position: absolute;
        top: 9px;
        left: -8px;
        width: 40px;
        height: 2px;
        background-color: #EA252C;
        transform: rotate(120deg);
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__actions {
    margin-top: 30px;
    padding-left: 40px;
  }

  /* О компании */
  &--about {
    background: linear-gradient(
      105deg,
      #F3F4F5 0%,
      #F3F4F5 57%,
      #EA252C 57.1%,
      #EA252C 64%,
      transparent 64.1%,
      transparent 64%,
    ),
    #232D3A
    url('../images/headline-about.png') no-repeat right top / contain;
  }

  /* Инвестору */
  &--investor {
    position: relative;
    overflow: hidden;

    &:after {
      z-index: 0;
      content: '';
      top: -10px;
      right: auto;
      left: 50%;
      width: 766px;
      height: 480px;
      background-image: url(../images/headline-investor-bg.png);
      transform: rotate(-1.75deg) translateX(-27%);
      background-size: cover;
    }
  }

  /* Торговый терминал */
  &--trading-terminal {
    background-image:
      url(../images/headline-terminal-page.png),
      linear-gradient(113deg, #F3F4F5 0%, #F3F4F5 49%, #232D3A 49.1%, #232D3A 100%);
    background-position: 50% 0;
    background-repeat: no-repeat;
    .headline__content {
      max-width: 490px;
    }

    .headline__actions {
      margin-top: 25px;

      .button {
        margin-right: 10px;
      }
    }
  }

  /* Торговый счет */
  &--trading-account {
    background: linear-gradient(
      113deg,
      #F3F4F5 0%,
      #F3F4F5 54%,
      #EA252C 54.1%,
      #EA252C 69%,
      #232D3A 69.1%,
      #232D3A 69%,
    );
    position: relative;
    overflow: hidden;

    .headline__actions {
      margin-top: 25px;
    }

    &:after {
      z-index: 0;
      content: '';
      top: auto;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 450px;
      background-image: url(../images/headline-trading-account-bg.png);
      background-position: 50% 100%;
      background-size: 1920px 493px;
    }
  }

  /* Партнёрам */
  &--partners {
    position: relative;
    overflow: hidden;
    background: linear-gradient(
      108.2deg,
      #F3F4F5 0%,
      #F3F4F5 56%,
      #EA252C 56.1%,
      #EA252C 70%,
      #232D3A 70.1%,
      #232D3A 70%,
    );

    .headline__content {
      max-width: 510px;

      p strong {
        display: block;
      }
    }

    &:after {
      z-index: 0;
      content: '';
      top: auto;
      bottom: 0;
      left: 50%;
      right: auto;
      width: 554px;
      height: 436px;
      background-image: url('../images/headline-partners-bg.png');
    }
  }

  /* Сохранность средств */
  &--safety {
    position: relative;
    background: linear-gradient(
      113deg,
      #F3F4F5 0%,
      #F3F4F5 56%,
      #EA252C 56.1%,
      #EA252C 70%,
      #232D3A 70.1%,
      #232D3A 70%,
    );

    &:before {
      position: absolute;
      content: '';
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-position: 70% 50%, 50% 100%;
      background-size: auto, cover;
      z-index: 0;
      background-image: url('../images/headline-safety.png'), url('../images/headline-map-bg.png');
    }

    .headline__content {
      max-width: 400px;
    }

    .headline__wrap {
      z-index: 2;
    }

    .headline-flag {
      max-width: 1180px;
      margin: 0 auto;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        background-color: #EA252C;
        width: 90px;
        height: 44px;
        transform: skewY(-12.1deg);
        bottom: -31px;
        left: -11px;
        z-index: -2;
      }
    }
  }
}

.headline-actions {
  display: flex;
  align-items: flex-start;
  align-content: flex-start;
  flex-direction: column;

  &__caption {
    font-family: 'Roboto';
    font-size: 16px;
    line-height: 16px;
    color: #6E7A86;
    margin-bottom: 18px;
  }

  &__icons {
    margin-bottom: 30px;
    display: flex;
    align-items: stretch;
    align-content: stretch;

    .icon-circle {
      margin-right: 30px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__buttons {
    display: flex;
    align-items: stretch;
    align-content: stretch;
  }

  .button {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.icon-circle {
  background-color: #7086A2;
  border-radius: 50%;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 40px;
  width: 40px;
  color: #ffffff;
}

.headline-features {
  display: flex;
  align-items: center;
  align-content: flex-start;
  margin-top: auto;
  @media #{$tablet} {
    max-width: 680px;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
  }
}

.headline-feature {
  font-family: 'Roboto';
  margin-right: 30px;
  &:first-child {

    .headline-feature__description {
      max-width: 100px;
    }
  }

  &:nth-child(3),
  &:last-child {
    margin-right: 0;
    @media #{$tablet} {
      .headline-feature__title {
        color: #fff;
      }
    }
  }

  &:nth-child(3) {
    .headline-feature__description {
      max-width: 180px;
    }
  }

  &__box {
    display: flex;
    align-items: center;
    align-content: center;
    @media #{$tablet} {
      flex-wrap: wrap;
    }
  }

  &__icon {
    color: #ffffff;
    @media #{$tablet} {
      margin: 0 auto 15px;
    }
  }

  &__description {
    margin-left: 20px;
    display: flex;
    align-items: flex-start;
    align-content: flex-start;
    flex-direction: column;
    max-width: 140px;
    @media #{$tablet} {
      align-items: center;
      text-align: center;
      max-width: 180px;
      margin-left: 0;
    }
  }

  &__title {
    font-size: 16px;
    line-height: 23px;
    font-weight: bold;
    color: #EA252C;
  }

  &__info {
    margin-top: 5px;
    font-size: 15px;
    line-height: 18px;
    color: #ffffff;
  }
}

