      #EA252C 52.1%,
      #EA252C 69%,
      #232D3A 69.1%,
      #232D3A 69%,
    );
    &:after {
      // background-size: auto 430px;
    }
    .headline__wrap {
      align-items: flex-start;
      align-content: flex-start;
      justify-content: start;
    }
    .h2 {
      font-size: 24px;
      line-height: 1.3;
    }
    &.headline .headline__content {
      p {
        padding-left: 20px;
        font-size: 14px;
        line-height: 1.4;
        strong {
          display: block;
        }
        &:before {
          width: 20px;
        }
      }
    }
    .headline__actions {
      padding-left: 20px;
      .icon-circle {
        margin-right: 15px;
        width: 33px;
        height: 33px;
        svg {
          max-width: 90%;
        }
      }
      .headline-actions__buttons {
        margin-left: -20px;
        flex-wrap: wrap;
        .button {
          min-width: 175px;
          margin-bottom: 7px;
        }
      }
    }
  }
}

@media (min-width: 471px) and (max-width: 678px) {
  .headline--investor:after {
    left: 30%;
  }
}
