.double-background {
  // sass-lint:disable-block indentation
  width: 100%;
  height: 320px;
  background:
    linear-gradient(105deg, #EA252C 48%, transparent 48.1%),
    linear-gradient(0deg, #F3F4F5 100%, transparent 50.1%);

  &__wrap {
    max-width: 1124px;
    margin: 0 auto;
    height: 100%;
    font-family: 'Roboto';

    @media #{$mobile} {
      max-width: $max-width-mobile;
    }

    @media #{$mobile-big} {
      max-width: $max-width-tablet;
    }

    @media #{$tablet} {}
  }

  .flex {
    height: 100%;
  }

  .h2 {
    margin-top: 0;
    margin-bottom: 0;
    color: #fff;
    font-size: 26px;
    line-height: 42px;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 5px;
      left: -40px;
      width: 40px;
      height: 2px;
      background-color: #ffffff;
      transform: rotate(120deg);
    }
  }

  p {
    font-size: 18px;
    line-height: 29px;
    color: #222B34;

    &:last-child {
      margin-bottom: 0px;
    }
  }

  &__caption {
    padding-right: 200px;
  }

  &__info {
    padding-left: 0;
  }
}

@media (max-width: 768px) {
  .double-background__wrap {
    max-width: 90%;
  }

  .double-background {
    .double-background__caption {
      max-width: 50%;
      padding-right: 5%;
      padding-left: 5%;
    }

    .double-background__info {
      padding-left: 5%;
    }

    .h2 {
      font-size: 20px;
      line-height: 1.8;
    }

    p {
      font-size: 16px;
      line-height: 1.8;
    }
  }
}

@media (max-width: 656px) {
  .double-background__wrap {
    max-width: 90%;
    flex-wrap: wrap;

    .col-half {
      flex-basis: unset;
    }
  }

  .double-background {
    background: linear-gradient(173deg, #EA252C 38%, transparent 38.1%), linear-gradient(0deg, #F3F4F5 100%, transparent 50.1%);
    height: auto;

    .double-background__wrap {
      .double-background__caption {
        max-width: 100%;
        padding: 7% 0 20% 20px;
      }

      .double-background__info {
        padding: 0 0 12%;
      }

      .h2 {
        max-width: 75%;
        min-width: 280px;
        font-size: 16px;
        line-height: 1.8;

        &:before {
          width: 30px;
          left: -28px;
        }
      }

      p {
        font-size: 14px;
        line-height: 1.8;
      }
    }
  }
}
