.finder-menu {
  display: flex;
  align-items: stretch;
  align-content: stretch;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style-type: none;
  font-family: 'Roboto';

  & .finder-menu {
    margin-top: 15px;

    .finder-menu__link {
      font-size: 13px;
      font-weight: 400;
      line-height: 13px;
      color: #6E7A86;

      &:hover {
        color: #222B34;
      }
    }
  }

  &__item {
    width: 100%;
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    width: 100%;
    display: inline-flex;
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;
    text-transform: uppercase;
    color: #222B34;
    border-bottom: 0;
    text-decoration: none;

    &:hover {
      color: #EA252C;
    }
  }
  &_mobile {
    .finder-menu__item {
      border-bottom: 1px solid #E5E5E5;
    }
  }
}


.finder-menu-icon {
  color: #EA252C;
  svg {
    color: #EA252C;
    fill: #EA252C !important;
  }
}


@media (max-width: 991px) {
  .header__cloud {
    background-color: rgba(0,0,0,.3);
    min-height: calc(100vh + 60px);
    .bottom-side {
      margin: auto auto 10px;
      width: 100%;
      max-width: 280px;
      margin-left: auto;
      margin-right: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      .user-action {
          display: flex;
          visibility: visible;
          opacity: 1;
          margin-left: 0;
      }
    }
    .header__language {
      margin-bottom: 25px;
      opacity: 1;
      visibility: visible;
      width: 100%;
      .language-list {
        position: static;
        opacity: 1;
        visibility: visible;
        display: flex;
        justify-content: space-between;
        background: transparent;
        box-shadow: none;
        overflow: visible;
        &__item {
          max-width: 45%;
        }
      }
    }
    .header-cloud__wrap {
      max-width: 420px;
      margin-right: auto;
      margin-left: 0;
      min-height: calc(100vh + 60px);
      padding-top: 0;
      padding-left: 0;
      background: #fff;
    }
  }
  .header__finder-item {
    padding-right: 0;
    flex-wrap: wrap;
    min-height: 100vh;
    .finder-menu {
      min-width: 100%;
      margin-bottom: 0;
      border-top: 1px solid #E5E5E5;
      &:first-child {
        border: 0;
      }
      & > li {
        a {
          padding: 20px 20px 15px 35px;
          font-size: 16px;
          font-weight: 500;
          &:before {
            content: '';
            display: inline-block;
            height: 22px;
            width: 5px;
            background: #EA252C;
            margin-left: -15px;
            margin-right: 15px;
            margin-top: -6px;
          }
        }
        li {
          a.finder-menu__link {
            padding: 15px 20px 10px 40px;
            font-size: 14px;
            font-weight: 500;
            color: #222B34;
            &:before {
              display: none;
            }
          }
        }
      }
      li {
        margin-bottom: 0;
        &.has_child {
          & > a {
            position: relative;
            &:after {
              font-size: 0;
              line-height: 0;
              content: url(../images/svg/arrow_bottom.svg);
              display: block;
              position: absolute;
              top: calc(50% - 2px);
              // transform: translateY(-50%);
              right: 20px;
            }
          }
        }
      }
      .finder-menu {
        // height: 0;
        // overflow: hidden;
        display: none;
        margin-top: 0;
        background-color: #F9F9F9;
        border-top: 1px solid #E5E5E5;
        padding-top: 10px;
        padding-bottom: 10px;
        li {
          border: 0;
        }
        li:last-child {
          border-bottom: 0;
        }
      }
    }
  }
}
