.popular-terminal {
  background-color: #232D3A;
  font-family: 'Roboto';
  padding-top: 50px;
  padding-bottom: 100px;
  overflow: hidden;

  &__wrap {
    max-width: 1180px;
    @media #{$tablet} {
      max-width: 710px;
      margin-left: auto;
    }
    margin: 0 auto;
    color: #ffffff;
  }

  &__info {
    // min-width: 650px;
    // width: 650px;
  }

  &__screen {
    // align-self: flex-start;
    // width: 780px;
    // min-width: 780px;

    .metatrader-picture {
      position: relative;
    }

    img {
      position: absolute;
      min-width: 725px;
      vertical-align: bottom;
      right: -50px;
    }
  }

  &__headline {

    .h2 {
      margin: 0;
      color: #A9B0B8;

      span {
        color: #ffffff;

        &.h2-break {
          display: block;
          color: #A9B0B8;
        }
      }
    }
  }

  &__body {
    margin-top: 45px;
    padding-right: 0;
    font-size: 18px;
    @media #{$tablet} {
      font-size: 16px;
    }
    line-height: 30px;
    font-weight: 300;
    position: relative;

    &:before {
      position: absolute;
      content: '';
      background: linear-gradient(
        174.54deg,
        #EA252C 5.8%,
        rgba(234, 37, 44, .86) 48.88%,
        rgba(234, 37, 44, 0) 88.43%
      );
      width: 145px;
      height: 92px;
      left: -18px;
      top: -22px;
      z-index: 0;
      transform: skewY(-4deg);
    }

    p {
      margin-bottom: 0;
      position: relative;
      z-index: 1;

      strong {
        font-weight: bold;
      }
    }
  }

  &__actions {
    margin-top: 30px;
  }
}

.popular-terminal-actions {
  display: flex;
  align-items: flex-start;
  align-content: flex-start;
  flex-direction: column;

  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    color: #FFFFFF;
    margin-bottom: 15px;
  }

  &__buttons {
    display: flex;
    align-items: stretch;
    align-content: stretch;

    .button {
      margin-right: 20px;
      font-size: 20px;
      line-height: 20px;

      &-with-icon__icon {
        margin-right: 13px;
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }
}


@media (max-width: 991px) {
  .popular-terminal__info {
    position: relative;
    z-index: 2;
  }
  .popular-terminal__screen {
    z-index: 1;
  }
  .popular-terminal .picture-box {
    position: relative;
    display: block;
    margin-left: -230px;
    img {
      min-width: calc(100% + 7vw);
      max-width: 100%;
    }
    &::before {
      display: block;
      content: '';
      position: absolute;
      z-index: 2;
      width: 80%;
      height: 100%;

      left: 0;
      top: 0;
      background: linear-gradient(89.76deg, #232D3A 0.15%, transparent 100%);
    }
  }
}


@media (max-width: 767px) {
  .popular-terminal__headline .h2 {
    max-width: 350px;
  }
  .popular-terminal__headline .h2 span {
    display: inline;
  }
}
