$trading-market-height:      660px;   // высота блока
$trading-market-deg:         4deg;    // угол поворота фона
$trading-market-bg-height:   63px;    // компенсация поворта
$trading-market-pt:          90px;    // отступ сверху
$trading-market-pb:          114px;   // отступ снизу

.trading-market {
  position: relative;
  min-height: $trading-market-height;
  height: $trading-market-height;
  padding-top: $trading-market-pt;
  padding-bottom: $trading-market-pb;
  margin-top: 120px;
  font-family: 'Roboto';
  @media #{$tablet} {
    height: 1060px;
  }
  @media #{$tablet_lt} {
    height: 1060px;
  }
  &:after {
    position: absolute;
    content: '';
    background-color: #232D3A;
    width: 100%;
    height: 90px;
    bottom: -4px;
    z-index: -1;
  }

  &__bg {
    position: absolute;
    width: 100%;
    height: calc(100% - #{$trading-market-bg-height});
    left: 0;
    top: 0;
    z-index: 0;

    &-red {
      background-color: #EA252C;
      transform: skewY(#{$trading-market-deg});
    }

    &-light {
      background-color: #F3F4F5;
      transform: skewY(-#{$trading-market-deg});
    }
  }

  &__wrap {
    position: relative;
    z-index: 1;
    max-width: 1180px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    @media #{$tablet} {
      align-items: flex-start;
      align-content: start;
      justify-content: start;
    }
  }

  &__content {
    position: relative;
    display: flex;
    align-items: stretch;
    align-content: stretch;
    min-height: 460px;
    max-height: 460px;
    @media #{$tablet} {
      max-height: unset;
      flex-wrap: wrap;
      align-items: flex-start;
      align-content: start;
      max-height: 850px;
      min-height: 850px;
      .col-half {
        width: 100%;
        flex-grow: unset;
        flex-basis: unset;
      }
      h2 {
        margin-bottom: 14px;
        font-size: 30px;
      }
    }
    @media #{$tablet_lt} {
      max-height: unset;
      flex-wrap: wrap;
      margin-top: 25px;
      align-items: flex-start;
      align-content: start;
      .col-half {
        width: 100%;
        flex-grow: unset;
        flex-basis: unset;
      }
      h2 {
        margin-bottom: 23px;
      }
    }
  }

  &__container {
    @media #{$tablet} {
      text-align: center;
      min-height: 240px;
    }
    @media #{$tablet_lt} {
      text-align: center;
      min-height: 340px;
    }
  }

  &__body {
    @media #{$tablet} {
      min-height: 240px;
    }
    @media #{$tablet_lt} {
      min-height: 340px;
    }
  }

  &__controls {
    position: relative;
    padding-right: 80px;
    @media #{$tablet} {
      padding-right: 0;
      order: 2;
    }
    @media #{$tablet_lt} {
      padding-right: 0;
      // order: 2;
    }
    &-img {
      position: absolute;
      top: 0;
      left: -30px;
      background-image: url('../images/svg/market-web.svg');
      background-repeat: no-repeat;
      background-size: contain;
      background-position: left left;
      width: 100%;
      max-width: 543px;
      height: 381px;
      @media #{$tablet_gt} {
      //   background-image: url('../images/svg/market-web_tablet.svg');
      //   background-position: 50% 50%;
      //   width: 442px;
      //   height: 442px;
        position: relative;
        left: unset;
        margin-left: 0;
        margin-right: auto;
        margin-bottom: 70px;
      }
      @media (max-width: 470px) {
        max-width: 320px;
        height: 250px;
        margin-bottom: 50px !important;
        margin-top: 40px;
        margin-left: auto !important;
      }
      // @media #{$tablet_lt} {
      //   background-image: url('../images/svg/market-web_tablet.svg');
      //   background-position: 50% 50%;
      //   width: 100%;
      //   max-width: 360px;
      //   position: relative;
      //   left: unset;
      //   margin-left: auto;
      //   margin-right: auto;
      // }
    }
  }

  &__body {
    @media #{$tablet_lt} {
      padding-left: 20px;
      padding-right: 20px;
    }
    .h3 {
      color: #222B34;
      font-size: 26px;
      line-height: 43px;
      text-transform: uppercase;
      padding: 0;
      margin: 0 0 30px 0;
      @media #{$tablet} {
        margin-bottom: 50px;
        font-size: 20px;
        line-height: 1.4;
      }
      @media #{$tablet_lt} {
        font-size: 16px;
        margin-bottom: 50px;
        margin-top: 40px;
      }
    }

    p {
      font-weight: 300;
      font-size: 18px;
      line-height: 29px;
      color: #222B34;
      position: relative;
      padding-left: 40px;

      &:before {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 40px;
        height: 2px;
        background-color: #EA252C;
        transform: rotate(120deg);
      }

      strong {
        font-weight: bold;
      }

      @media #{$tablet} {
        padding-left: 0;
        &::before {
          left: 50%;
          width: 30px;
          transform: translateX(-50%) rotate(120deg);
          top: -30px;
        }
      }
      @media #{$tablet_lt} {
        padding-left: 0;
        font-size: 14px;
        line-height: 1.6;
        &::before {
          left: 50%;
          width: 20px;
          transform: translateX(-50%) rotate(120deg);
          top: -25px;
        }
      }
    }
  }

  &__actions {
    padding-left: 40px;
    .trading-market__controls & {
      display: none;
      text-align: center;
    }
    @media #{$tablet} {
      .trading-market__container & {
        display: none;
      }
      .trading-market__controls & {
        margin-top: 50px;
        display: block;
        padding-left: 0;
      }
    }
    // @media #{$tablet_lt} {
    //   .trading-market__container & {
    //     padding-left: 0;
    //   }
    //   .trading-market__controls & {
    //     display: none;
    //   }
    // }
  }
}

.t-market-control {
  $t-market-control: &;
  position: relative;
  text-decoration: none;
  border: none;
  font-family: 'Roboto';
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  color: #7086A2;

  &__box {
    position: relative;
    // @media #{$tablet} {
    //   display: flex;
    //   flex-wrap: wrap;
    // }
    // @media #{$tablet_lt} {
    //   display: flex;
    //   flex-wrap: wrap;
    // }
  }

  &__icon {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    border-radius: 50%;
    background-color: #7086A2;
    width: 60px;
    height: 60px;
    color: #ffffff;
  }

  &__name {
    font-weight: bold;
    display: inline-block;
    text-align: center;
    @media #{$tablet} {
      font-size: 12px;
    }
    @media #{$tablet_lt} {
      font-size: 12px;
    }
  }

  &.is-active {
    color: #E92B22;

    #{$t-market-control} {

      &__icon {
        background-color: #E92B22;
        // width: 72px;
        // height: 72px;
      }
    }
  }

  // список и позиции отдельных кнопок
  &--bitcoin {
    position: absolute;
    right: -10px;
    top: 140px;

    #{$t-market-control} {

      &__icon {
        width: 72px;
        height: 72px;
        // @media #{$tablet} {
        //   width: 48px;
        //   height: 48px;
        // }
        svg {
          position: relative;
          right: -2px;
        }
      }

      &__name {
        position: absolute;
        left: 0;
        top: 5px;
        width: 75px;
        text-align: center;
      }
    }
  }

  &--currencies {
    position: absolute;
    left: 115px;
    top: 68px;
    #{$t-market-control} {

      &__icon {
        width: 72px;
        height: 72px;
      }

      &__name {
        position: relative;
        left: -22px;
        top: 8px;
      }
    }
  }

  // акции
  &--cfdstock {
    position: absolute;
    top: 20px;
    right: 20px;

    #{$t-market-control} {

      &__icon {
        width: 61px;
        height: 61px;
      }

      &__name {
        position: relative;
        left: 79px;
        top: -13px;
        text-align: left;
        @media (max-width: 679px) {
          left: -65px;
          top: -50px;
          text-align: right;
        }
      }
    }
  }

  // металлы
  &--cfdmetals {
    position: absolute;
    right: 37px;
    bottom: -53px;

    #{$t-market-control} {

      &__icon {
        width: 85px;
        height: 85px;

        svg {
          position: relative;
          top: -3px;
        }
      }

      &__name {
        position: relative;
        left: 95px;
        top: -53px;
        text-align: left;
        @media (max-width: 679px) {
          text-align: right;
          left: -70px;
          top: -30px;
        }
      }
    }
  }

  // индексы
  &--cfdindexes {
    position: absolute;
    left: 26px;
    bottom: -1px;
    @media (max-width: 679px) {
      bottom: 0;
    }
    #{$t-market-control} {

      &__icon {
        width: 60px;
        height: 60px;
      }

      &__name {
        position: relative;
        left: -64px;
        top: 13px;
        @media (max-width: 679px) {
          left: 0;
        }
      }
    }
  }

  // товары
  &--cfdproducts {
    position: absolute;
    left: 211px;
    bottom: 37px;

    #{$t-market-control} {

      &__icon {
        width: 48px;
        height: 48px;
      }

      &__name {
        position: absolute;
        top: 27px;
        left: -8px;
        br {
          display: none;
        }
      }
    }
  }
}

