.top-traders {
  height: 690px;
  position: relative;
  .owl-wrap_outside {
    overflow: hidden;
  }
  @media #{$tablet_lt} {
    height: 860px;
  }

  &__bg {
    position: absolute;
    z-index: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
  }

  &__wrap {
    padding-top: 80px;
    max-width: 1180px;
    margin: 0 auto;
    position: relative;
    z-index: 1;

    // @media only screen and (max-width: 1024px) {
    //   max-width: 100%;
    //   overflow: hidden;
    // }
    @media #{$mobile} {
      max-width: $max-width-mobile;
    }
    @media #{$tablet_lt} {
      flex-wrap: wrap;
      padding-top: 0;
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__info {
    margin-right: 40px;
    min-width: 361px;
    @media #{$tablet_lt} {
      min-width: 100%;
    }

    .intro-box {
      @media #{$tablet_lt} {
        margin-bottom: 40px;
      }
      .h2 {
        margin-bottom: 55px;
        position: relative;

        &:after {
          content: '';
          position: absolute;
          bottom: -25px;
          left: -10px;
          width: 40px;
          height: 2px;
          background-color: #EA252C;
          transform: rotate(120deg);
        }
        @media #{$tablet_lt} {
          margin-bottom: 25px;
          span {
            display: block;
          }
          &:after {
            width: 20px;
            left: 0;
            bottom: -34px;
            transform: rotate(118deg);
          }
        }
      }
    }

    p {
      max-width: 300px;
      @media #{$tablet_lt} {
        padding-left: 20px;
        font-size: 14px;
        line-height: 2;
      }
      @media (max-width: 767px) {
        max-width: 85%;
        min-width: 300px;
      }
    }
  }

  &__actions {
    margin-top: 55px;

    @media #{$tablet_lt} {
      margin: 30px auto 0;
    }
  }

  &__list {
    max-width: 810px;
    min-width: 810px;
    width: 810px;
    flex-basis: 810px;
    position: relative;

    .traders-list__item {
      width: 250px;
      margin-right: 20px;
    }

  }
}
.traders-slider__left,
.traders-slider__right {
  bottom: auto;
  top: 50%;
  transform: translateY(-50%);
}
